import React from 'react';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';
import { validateLogin } from './helpers/auth';
import ContactsList from './components/ContactsList/ContactsList';
import AuthForm from './components/AuthForm/AuthForm';

function routes() {
  const isLogged = validateLogin(localStorage.password);

  const publicRoutes = <Switch>
    <Route path={`${process.env.PUBLIC_URL}/login`} exact component={AuthForm} />
    <Redirect from="*" to={`${process.env.PUBLIC_URL}/login`}/>
  </Switch>;

  const privateRoutes = <Switch>
    <Route path={`${process.env.PUBLIC_URL}/contacts`} exact component={ContactsList} />
    <Route path={`${process.env.PUBLIC_URL}/contacts/new`} exact component={ContactsList} />
    <Redirect from="*" to={`${process.env.PUBLIC_URL}/contacts`} />
  </Switch>;

  return <BrowserRouter>
    { isLogged ? privateRoutes : publicRoutes }
  </BrowserRouter>
}

export default routes;